/* === Search Modal Styles === */
:root {
  --burgundy: #8B0000;
  --burgundy-light: #A52A2A;
  --gold: #FFD700;
  --gold-dark: #B8860B;
  --dark: #1a1a1a;
  --dark-light: #2d2d2d;
  --text-light: #e0e0e0;
  --text-grey: #9ca3af;
  --shadow-dark: 0 4px 20px rgba(139, 0, 0, 0.3);
  --shadow-gold: 0 4px 20px rgba(255, 215, 0, 0.2);
}

/* 검색 모달 오버레이 */
.search-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(8px);
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: modalSlideIn 0.3s ease-out;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: scale(0.9) translateY(-20px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

/* 검색 모달 */
.search-modal {
  background: linear-gradient(135deg, var(--dark-light) 0%, var(--dark) 100%);
  border: 2px solid var(--burgundy);
  border-radius: 20px;
  padding: 30px;
  width: 90%;
  max-width: 500px;
  position: relative;
  overflow: hidden;
  box-shadow: var(--shadow-dark);
}

.search-modal::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, var(--gold), var(--burgundy), var(--gold));
  animation: curtainLine 3s ease-in-out infinite;
}

@keyframes curtainLine {
  0%, 100% { opacity: 0.3; }
  50% { opacity: 1; }
}

/* 커튼 장식 */
.curtain-decoration {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 60px;
  height: 60px;
  pointer-events: none;
}

.curtain-decoration::before,
.curtain-decoration::after {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: linear-gradient(45deg, var(--burgundy), var(--gold));
  animation: curtainFloat 4s ease-in-out infinite;
}

.curtain-decoration::before {
  top: 0;
  left: 0;
  animation-delay: 0s;
}

.curtain-decoration::after {
  bottom: 0;
  right: 0;
  animation-delay: 2s;
}

@keyframes curtainFloat {
  0%, 100% { transform: translateY(0) scale(1); opacity: 0.7; }
  50% { transform: translateY(-10px) scale(1.1); opacity: 1; }
}

/* 닫기 버튼 */
.close-btn {
  position: absolute;
  top: 15px;
  right: 15px;
  background: linear-gradient(135deg, var(--burgundy), var(--burgundy-light));
  border: 2px solid var(--gold);
  color: var(--gold);
  width: 32px;
  height: 32px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.close-btn:hover {
  background: linear-gradient(135deg, var(--gold), var(--gold-dark));
  border-color: var(--burgundy);
  color: var(--burgundy);
  transform: scale(1.1);
  box-shadow: var(--shadow-gold);
}

/* 검색 입력 영역 */
.search-input-area {
  display: flex;
  gap: 10px;
  margin-bottom: 25px;
  margin-top: 10px;
}

.search-input {
  flex: 1;
  background: var(--dark);
  border: 2px solid var(--burgundy);
  color: var(--text-light);
  padding: 12px 16px;
  border-radius: 25px;
  font-size: 16px;
  transition: all 0.3s ease;
}

.search-input:focus {
  outline: none;
  border-color: var(--gold);
  box-shadow: 0 0 20px rgba(255, 215, 0, 0.3);
}

.search-input::placeholder {
  color: var(--text-grey);
}

.search-btn {
  background: linear-gradient(135deg, var(--burgundy), var(--burgundy-light));
  border: 2px solid var(--gold);
  color: var(--gold);
  padding: 12px 24px;
  border-radius: 25px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.search-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 215, 0, 0.2), transparent);
  transition: left 0.5s ease;
}

.search-btn:hover::before {
  left: 100%;
}

.search-btn:hover {
  background: linear-gradient(135deg, var(--gold), var(--gold-dark));
  border-color: var(--burgundy);
  color: var(--burgundy);
  transform: translateY(-2px);
  box-shadow: var(--shadow-gold);
}

/* 실시간 검색어 */
.top-search-box {
  background: var(--dark);
  border: 1px solid var(--burgundy);
  border-radius: 15px;
  padding: 20px;
}

.top-search-header {
  color: var(--gold);
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 15px;
  text-align: center;
  text-shadow: 0 0 10px rgba(255, 215, 0, 0.3);
}

.top-search-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.top-search-list li {
  display: flex;
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid rgba(139, 0, 0, 0.3);
  transition: all 0.3s ease;
  cursor: pointer;
}

.top-search-list li:last-child {
  border-bottom: none;
}

.top-search-list li:hover {
  background: rgba(139, 0, 0, 0.1);
  padding-left: 10px;
  border-radius: 8px;
}

.top-search-list .rank {
  background: linear-gradient(135deg, var(--burgundy), var(--burgundy-light));
  color: var(--gold);
  width: 24px;
  height: 24px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: 600;
  margin-right: 15px;
  flex-shrink: 0;
}

.top-search-list span:last-child {
  color: var(--text-light);
  font-size: 14px;
}

/* 반응형 디자인 */
@media (max-width: 768px) {
  .search-modal {
    width: 95%;
    padding: 25px;
  }
  
  .search-input-area {
    flex-direction: column;
  }
  
  .search-btn {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .search-modal {
    padding: 20px;
  }
  
  .top-search-box {
    padding: 15px;
  }
}
